@import url('https://fonts.googleapis.com/css2?family=Do+Hyeon&family=Gowun+Dodum&display=swap');
:root{
  --ui-color-01 : 237, 242, 251;
  --ui-color-02 : 226, 234, 252;
  --ui-color-03 : 215, 227, 252;
  --ui-color-04 : 204, 219, 253;
  --ui-color-05 : 193, 211, 254;
  --ui-color-06 : 182, 204, 254;
  --ui-color-07 : 171, 196, 255;
  font-size: 34px;
}


h2{
  color: rgb(var(--ui-color-07));
}


* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: 'Do Hyeon', sans-serif;
  /* font-family: 'Gowun Dodum', sans-serif; */
  
  
  transition: 0.3s;
}

p{
  font-family: 'Gowun Dodum', sans-serif;
  
}

ul, ol{
  list-style-type: none;
}

/* 여기부터 header css */

header{
  position: sticky;
  padding-top: 30px;
  padding-bottom: 30px;
  display: flex;
  justify-content: center;
  align-items: center;
  top: 0;
  z-index: 999;
  width: fit-content;
  margin: 0 auto;
}

.menu{
  padding: 12px;
  padding-bottom: 0;
  display: flex;
  justify-content: center;
  column-gap: 30px;
  align-items: baseline;
  text-align: center;

  background-color: rgba(256,256,256, 0.6);
  backdrop-filter: blur(6px);
  border-radius: 50px;

}

.menu-item{
  position: relative;
}

.sub-menu-wrap{
  padding-top: 12px;
}

.sub-menu{
  flex-direction: column;
  position: absolute; 
  
  width: max-content;
  display: flex;
  row-gap: 4px;
  padding: 5px 10px;

  display: none;

  background-color: rgba(256,256,256);
  border-radius: 5px;
}

div.line{
  border: 1px solid black;
}

.sub-menu li{
  border-radius: 5px;
  padding: 5px 10px;
  cursor: pointer;
}
.sub-menu li:hover, .sub-menu li.active{
  background-color: silver;

}

.sub-menu .line:last-child{
  display: none;
}

.menu .menu-item:hover .sub-menu{
  display: flex;
}

.menu-title{
  border-radius: 50px;
  padding: 5px 20px;
  cursor: pointer;
  display: flex;
  align-items: center;
  column-gap: 8px;
}

.menu-item:hover > .menu-title , .menu-title.active{
  background-color: white;
}

/* 여기까지 header css */

/* 여기부터 footer css */

footer{
  display: flex;
  flex-direction: column;
  row-gap: 20px;
  justify-content: center;
  align-items: center;
  padding: 60px 0;
}

.footer-contacts{
  display: flex;
  column-gap: 30px;
  justify-content: center;
}


.footer-contacts button{
  background: none;
  border: none;
  font-size: 70px;
  display: flex;
  justify-content: center;
  align-items: center;
  cursor: pointer;
}

/* 여기까지 footer css */


/* 여기부터 main 페이지 디자인 */


main > section{
  padding: 20px 70px;
}

/* * main 페이지 한줄 소개 section */
.one-line-section{
  height: 200px;
  padding: 0;
}

.one-line-section > div{
  position: absolute;
  right: 0;
  left: 0;
  height: 200px;
}

.one-line-section img{
  width: 100%;
  height: 100%;
  object-fit: cover;
  object-position: bottom;
}

.one-line-section > div> div{
  position: absolute;
  top: 0;
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;

}

/* main 페이지 MySkills 섹션 */
.my-skills-section .character{
  position: relative;
  width: 500px;
  min-height: 300px;
  background-color: #FFD2D2;
}

.pung{
  font-size: 30px;
  position: absolute;
  background-color: rgb(var(--ui-color-03));
  border-radius: 20px;
  padding: 20px;
  top: 10px;
  left: 250px;
  width: 400px;
}
.pung::before{
  content:""; 
  position: absolute; 
  top: 70px; 
  left: -20px; 
  border-right: 30px solid rgb(var(--ui-color-03)); 
  border-top: 10px solid transparent; 
  border-bottom: 10px solid transparent;
  transform: rotate(-30deg);
}

.my-skills-wrap{
  display: flex;
  margin-top: 20px;

  border-radius: 50px;
  overflow: hidden;

}

.my-skills-contents{
  flex-grow: 1;
  border-top: 6px solid #FFD2D2;
  border-bottom: 6px solid #FFD2D2;
  border-right: 6px solid #FFD2D2;
  border-top-right-radius: 50px;
  border-bottom-right-radius: 50px;
}

.my-skills-contents{
  padding: 10px;
  display: flex;
  flex-direction: row-reverse;
  align-items: stretch;

  column-gap: 30px;

}

.skill-box{
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  row-gap: 10px;
  font-size: 35px;
}

.progress-back{
  width: 20px;
  flex-grow: 1;
  background-color: silver;
  border-top-right-radius: 10px;
  border-top-left-radius: 10px;
  display: flex;
  flex-direction: column;
  justify-content: flex-end;

  position: relative;
}

.skill-box .progress-bar{
  border-top-right-radius: 10px;
  border-top-left-radius: 10px;
  
}
.skill-box:nth-child(1) .progress-bar{
  animation-name: progress-100;
  animation-duration: 4s;
  animation-iteration-count: infinite;
  animation-direction: alternate;
  background-color: rgb(var(--ui-color-07));
}
.skill-box:nth-child(1) .progress-bar::after{
  content: '100%';
  font-size: 30px;
  position: absolute;
  top: 0;
  left: -90%;
  transform: rotate(-45deg);
}

.skill-box:nth-child(2) .progress-bar{
  animation-direction: alternate;
  animation-name: progress-100;
  animation-duration: 5s;
  animation-iteration-count: infinite;
  background-color: rgb(var(--ui-color-03));
}
.skill-box:nth-child(2) .progress-bar::after{
  content: '100%';
  font-size: 30px;
  position: absolute;
  top: 0;
  left: -90%;
  transform: rotate(-45deg);
}
.skill-box:nth-child(3) .progress-bar{
  animation-name: progress-80;
  animation-duration: 6s;
  animation-direction: alternate;
  animation-iteration-count: infinite;
  background-color: rgb(162, 0, 255);
}
.skill-box:nth-child(3) .progress-bar::after{
  content: '80%';
  font-size: 30px;
  position: absolute;
  top: 0;
  left: -90%;
  transform: rotate(-45deg);
}
.skill-box:nth-child(4) .progress-bar{
  animation-name: progress-40;
  animation-duration: 7s;
  animation-iteration-count: infinite;
  background-color: rgb(var(--ui-color-05));
  animation-direction: alternate;
}
.skill-box:nth-child(4) .progress-bar::after{
  content: '40%';
  font-size: 30px;
  position: absolute;
  top: 0;
  left: -90%;
  transform: rotate(-45deg);
}
.skill-box:nth-child(5) .progress-bar{
  animation-name: progress-100;
  animation-duration: 8s;
  animation-iteration-count: infinite;
  background-color: rgb(0, 145, 255);
  animation-direction: alternate;
}
.skill-box:nth-child(5) .progress-bar::after{
  content: '100%';
  font-size: 30px;
  position: absolute;
  top: 0;
  left: -90%;
  transform: rotate(-45deg);
}
@keyframes progress-100{
  from{
    height: 0;
  }
  to {
    height: 100%
  }
}
@keyframes progress-80{
  from{
    height: 0;
  }
  to {
    height: 80%
  }
}
@keyframes progress-40{
  from{
    height: 0;
  }
  to {
    height: 40%
  }
}

/* project- section 디자인 */
.project-wrap{
  display: flex;

  column-gap: 70px;

  margin-bottom: 10px;

}
.project-wrap:nth-child(odd){
  flex-direction: row-reverse;
}

.project-wrap .project-img{
  width: 700px;
  flex-shrink: 0;
}

.project-wrap img{
  width: 100%;
  height: 100%;
  object-fit: cover;
  object-position: center;
}

.project-contents{
  display: flex;
  flex-direction: column;
  flex-grow: 1;
  justify-content: space-between;
  padding: 20px 0;
}

.project-contents button{
  align-self: flex-end;
  cursor: pointer;
  padding: 10px 20px;
}

.contact-form{
  padding: 100px;
  background-color: #36454f;
  box-shadow: 4px 4px 2px rgba(0,0,0,0.8);
  border-radius: 15px;
  color: rgb(var(--ui-color-02));

  display: flex;
  justify-content: flex-start;
  align-items: center;
  column-gap: 60px;
}
.contact-form .line{
  align-self: stretch;
}

.contact-inputs{
  flex-grow: 1;
  display: flex;
  flex-direction: column;
  
  row-gap: 70px;
}

.contact-inputs label{
  display: flex;
  column-gap: 20px;
  align-items: flex-start;
  
}
.contact-inputs label input{
  flex-grow: 1;
  background: none;
  padding: 20px;
  font-size: 40px;
  border: 1px solid white;
  color: rgb(var(--ui-color-01));
}

.contact-inputs label textarea{
  height: 500px;
  flex-grow: 1;
  resize: none;
  background: none;
  padding: 20px;
  border: 1px solid white;
  color: rgb(var(--ui-color-01));
  font-size: 40px;
}

/* 여기까지 main 페이지 디자인 */

/* 여기부터 pcc data 페이지 디자인 */
.data-title{
  text-align: center;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  row-gap: 30px;
}
.pcc-img{
  padding: 0;
  width: 100%;
  height: 700px;
}

.pcc-contents{
  width: 800px;
  margin: 0 auto;
}

.pcc-img img{
  width: 100%;
  height: 100%;
  object-fit: cover;
  object-position: center;
}